@import url(https://fonts.googleapis.com/css2?family=Teko&display=swap);
.navBarTemplate header {
    height: 12vh;
    background-color: var(--grey);
}

.navBarTemplate header .container {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: space-between;
            justify-content: space-between;

    width: 100%;
    max-width: 1200px;

    margin: auto;
}

.navBarTemplate header img {
    margin-left: 1%;
    width: 200px;
    padding: 15px 0;
}

.navBarTemplate header nav ul {
    display: -webkit-flex;
    display: flex;
}

.navBarTemplate header nav ul li {
    list-style: none;
}

.navBarTemplate header nav ul li a {
    text-decoration: none;
    color: var(--light-green);
    font-size: 1rem;
    padding: 2rem 1rem 1.95rem;
    transition: all 250ms linear 0s;
}

.navBarTemplate header nav ul li a:hover {
    background: rgba(255, 255, 255, 0.15);
}

@media (max-width: 770px) {

    .navBarTemplate nav {
        display: none;
    }

    .navBarTemplate .one,
    .navBarTemplate .two,
    .navBarTemplate .three {
        background-color: var(--light-green);
        height: 5px;
        width: 100%;
        margin: 6px auto;

        transition-duration: 0.3s;
    }

    .navBarTemplate .menu-toggle {
        width: 40px;
        height: 30px;
        margin-right: 10px;
    }

    .navBarTemplate .menu-section.on {
        position: absolute;
        top: 0;
        left: 0;

        width: 100vw;
        height: 100vh;

        background-color: var(--grey);

        z-index: 10;

        display: -webkit-flex;

        display: flex;
        -webkit-justify-content: center;
                justify-content: center;
        -webkit-align-items: center;
                align-items: center;
    }

    .navBarTemplate .menu-section.on nav {
        display: block;
    }

    .navBarTemplate .menu-section.on .menu-toggle {
        position: absolute;
        right: 25px;
        top: 15px;
    }

    .navBarTemplate .menu-section.on .menu-toggle .one {
        -webkit-transform: rotate(45deg) translate(7px, 7px);
                transform: rotate(45deg) translate(7px, 7px);
    }

    .navBarTemplate .menu-section.on .menu-toggle .two {
        opacity: 0;
    }

    .navBarTemplate .menu-section.on .menu-toggle .three {
        -webkit-transform: rotate(-45deg) translate(8px, -9px);
                transform: rotate(-45deg) translate(8px, -9px);
    }

    .navBarTemplate .menu-section.on nav ul {
        text-align: center;
        display: block;
    }

    .navBarTemplate .menu-section.on nav ul a {
        transition-duration: 0.5s;
        font-size: 2rem;
        line-height: 1rem;
        display: block;
    }
}


.homeTemplate{
    height: 88vh;
    background-image: url(/static/media/introBackground.3a167b04.gif);
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    background-color: var(--green);
    background-blend-mode: color-dodge;
}

.homeTemplate .title{
    text-transform: uppercase;
    letter-spacing: 0.3rem;
    padding: 30vh 0 0;
    text-align: center;
    color: var(--grey);
}

.homeTemplate .title h1{
    font-size: 4em;
}

@media (max-width: 720px){
    .homeTemplate .title h1{
        font-size: 3em;
    }
}
.sobreTemplate {
    height: 88vh;
    background-color: black;
}

.sobreTemplate img {
    width: 350px;
    border: double 4px transparent;
    border-radius: 50%;
    background-image: linear-gradient(white, white), radial-gradient(circle at top, #83DB02, #BAF800);
    background-origin: border-box;
    background-clip: content-box, border-box;
    margin: 12.5% 22.5%;
}

.sobreTemplate .col-2 ul{
    margin-top: 12.5%;
    list-style: square;
    color: var(--lime);
}

.sobreTemplate .col-2 ul li p{
    font-size: 95%;
    line-height: 2.4rem;
    letter-spacing: 0.2rem;
}

.sobreTemplate .col-2{
    width: 48.5%;
    float: left;
}

@media (max-width: 770px){

    .sobreTemplate{
        height: 100%;
    }

    .sobreTemplate img{
        margin: 1.5% 25% 0;
    }

    .sobreTemplate .col-2{
        float: none;
        width: 100%;
    }

    .sobreTemplate .col-2 ul{
        margin-top: 5%;
        padding-bottom: 23vh;
    }

    .sobreTemplate .col-2 ul li p{
        text-align: center;
    }
}

@media (max-width: 425px){
    .sobreTemplate img {
        width: 200px;
        margin: 12.5% 17.5%;
    }
}
