.progressBarTemplate{
	margin-top: 4%;
    height: 55px;
    width: 100%;
}

.progressBarTemplate h1{
    margin-bottom: 4%;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    font-size: 1.15rem;
    color: var(--lime);
}
.progressBarTemplate .progress {
	background-color: #1d1d1d;
	border-radius: 20px;
	position: relative;
	height: 20px;
	width: 100%;
}

.progressBarTemplate .progress-done {
	padding: 0.5rem;
	font-size: 1.15rem;
	font-family: 'Teko', sans-serif;
	background: linear-gradient(to left, var(--green), var(--light-green));
	border-radius: 20px;
	color: #fff;
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-justify-content: center;
	        justify-content: center;
	height: 100%;
	width: 0;
	opacity: 0;
	transition: 1s ease 0.3s;
}
.portfolioTemplate{
    padding: 2%;
    height: 88vh;
    width: 100vw;
    overflow-x: hidden;
    background-color: black;
    /* background-color: black; */
}

.portfolioTemplate .cards{
    margin-left: 8%;
    border-radius: 20px;
    float: left;
    margin-bottom: 8%;
    height: 65vh;
    width: 35vw;
    background-color: var(--grey);
    color: var(--light-green);
}

.portfolioTemplate .cards img{
    border-radius: 20px 20px 0 0;
    height: 50%;
    width: 100%;
}

.portfolioTemplate .cards h1{
    text-align: center;
    margin-bottom: 2%;
}

.portfolioTemplate .cards p{
    padding: 0 3.5%;
    margin-bottom: 3%;
}

.portfolioTemplate .cards a{
    margin: 0 30%;
    font-weight: bold;
    border-radius: 25px;
    width: 40%;
    padding: 2% 1%;
    background-image: linear-gradient(to right, var(--light-green) , var(--green));
    color: black;
    text-decoration: none;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    transition: all 250ms linear 0s;
}

.portfolioTemplate .cards a:hover{
    color: white;
    background-image: linear-gradient(to right, var(--green) , var(--dark-green));
}

@media (max-width: 720px){
    .portfolioTemplate .cards{
        font-size: 110%;
        margin-left: 0;
        width: 96vw;
        height: 100vh;
        float: none;
    }

    .portfolioTemplate .cards a{
        margin-top: 10%;
        text-align: center;
    }
}


.contatoTemplate{
    padding: 5%;
    height: 88vh;
    background-color: black;
}

.contato{
    margin: 5% 32%;
    border-radius: 25px;
    padding: 2%;
    width: 36%;
    background-color: white;
}

.contatoTemplate .contato ul{
    display: -webkit-flex;
    display: flex;
    list-style: none;
        margin: 25px;
}

.contatoTemplate .contato ul li:last-child{
    margin-left: 10%;
}

.contatoTemplate .contato ul li a img{
    height: 150px;
    transition: all 0.2s linear;
}

.contatoTemplate .contato ul li a img:hover{
    -webkit-transform: translateY(-1rem);
            transform: translateY(-1rem);
}

@media (max-width: 1024px){
    .contatoTemplate{
        padding: 5%;
        height: 100vh;
        background-color: black;
    }

    .contatoTemplate .contato ul li a img{
        height: 15vh;
        transition: all 0.2s linear;
    }

    .contato{
        margin: 25% 0;
        border-radius: 25px;
        padding: 2%;
        width: 100%;
        background-color: white;
    }
}
