.portfolioTemplate{
    padding: 2%;
    height: 88vh;
    width: 100vw;
    overflow-x: hidden;
    background-color: black;
    /* background-color: black; */
}

.portfolioTemplate .cards{
    margin-left: 8%;
    border-radius: 20px;
    float: left;
    margin-bottom: 8%;
    height: 65vh;
    width: 35vw;
    background-color: var(--grey);
    color: var(--light-green);
}

.portfolioTemplate .cards img{
    border-radius: 20px 20px 0 0;
    height: 50%;
    width: 100%;
}

.portfolioTemplate .cards h1{
    text-align: center;
    margin-bottom: 2%;
}

.portfolioTemplate .cards p{
    padding: 0 3.5%;
    margin-bottom: 3%;
}

.portfolioTemplate .cards a{
    margin: 0 30%;
    font-weight: bold;
    border-radius: 25px;
    width: 40%;
    padding: 2% 1%;
    background-image: linear-gradient(to right, var(--light-green) , var(--green));
    color: black;
    text-decoration: none;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all 250ms linear 0s;
}

.portfolioTemplate .cards a:hover{
    color: white;
    background-image: linear-gradient(to right, var(--green) , var(--dark-green));
}

@media (max-width: 720px){
    .portfolioTemplate .cards{
        font-size: 110%;
        margin-left: 0;
        width: 96vw;
        height: 100vh;
        float: none;
    }

    .portfolioTemplate .cards a{
        margin-top: 10%;
        text-align: center;
    }
}

