.competenciasTemplate{
    padding: 1% 3%;
    height: 88vh;
    background-color: black;
}

.competenciasTemplate .col-2{
    width: 47%;
    float: left;
}

.col-margin{
    margin-left: 5%;
}

@media (max-width: 720px){
    .competenciasTemplate{
        height:100%;
    }
    .competenciasTemplate .col-2{
        width: 100%;
        float: none;
    }

    .col-margin{
        margin-left: 0;
    }
}