.sobreTemplate {
    height: 88vh;
    background-color: black;
}

.sobreTemplate img {
    width: 350px;
    border: double 4px transparent;
    border-radius: 50%;
    background-image: linear-gradient(white, white), radial-gradient(circle at top, #83DB02, #BAF800);
    background-origin: border-box;
    background-clip: content-box, border-box;
    margin: 12.5% 22.5%;
}

.sobreTemplate .col-2 ul{
    margin-top: 12.5%;
    list-style: square;
    color: var(--lime);
}

.sobreTemplate .col-2 ul li p{
    font-size: 95%;
    line-height: 2.4rem;
    letter-spacing: 0.2rem;
}

.sobreTemplate .col-2{
    width: 48.5%;
    float: left;
}

@media (max-width: 770px){

    .sobreTemplate{
        height: 100%;
    }

    .sobreTemplate img{
        margin: 1.5% 25% 0;
    }

    .sobreTemplate .col-2{
        float: none;
        width: 100%;
    }

    .sobreTemplate .col-2 ul{
        margin-top: 5%;
        padding-bottom: 23vh;
    }

    .sobreTemplate .col-2 ul li p{
        text-align: center;
    }
}

@media (max-width: 425px){
    .sobreTemplate img {
        width: 200px;
        margin: 12.5% 17.5%;
    }
}