.contatoTemplate{
    padding: 5%;
    height: 88vh;
    background-color: black;
}

.contato{
    margin: 5% 32%;
    border-radius: 25px;
    padding: 2%;
    width: 36%;
    background-color: white;
}

.contatoTemplate .contato ul{
    display: flex;
    list-style: none;
        margin: 25px;
}

.contatoTemplate .contato ul li:last-child{
    margin-left: 10%;
}

.contatoTemplate .contato ul li a img{
    height: 150px;
    transition: all 0.2s linear;
}

.contatoTemplate .contato ul li a img:hover{
    transform: translateY(-1rem);
}

@media (max-width: 1024px){
    .contatoTemplate{
        padding: 5%;
        height: 100vh;
        background-color: black;
    }

    .contatoTemplate .contato ul li a img{
        height: 15vh;
        transition: all 0.2s linear;
    }

    .contato{
        margin: 25% 0;
        border-radius: 25px;
        padding: 2%;
        width: 100%;
        background-color: white;
    }
}