@import url('https://fonts.googleapis.com/css2?family=Teko&display=swap');

.progressBarTemplate{
	margin-top: 4%;
    height: 55px;
    width: 100%;
}

.progressBarTemplate h1{
    margin-bottom: 4%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.15rem;
    color: var(--lime);
}
.progressBarTemplate .progress {
	background-color: #1d1d1d;
	border-radius: 20px;
	position: relative;
	height: 20px;
	width: 100%;
}

.progressBarTemplate .progress-done {
	padding: 0.5rem;
	font-size: 1.15rem;
	font-family: 'Teko', sans-serif;
	background: linear-gradient(to left, var(--green), var(--light-green));
	border-radius: 20px;
	color: #fff;
	display: flex;
	align-items: center;
	justify-content: center;
	height: 100%;
	width: 0;
	opacity: 0;
	transition: 1s ease 0.3s;
}