.navBarTemplate header {
    height: 12vh;
    background-color: var(--grey);
}

.navBarTemplate header .container {
    display: flex;
    align-items: center;
    justify-content: space-between;

    width: 100%;
    max-width: 1200px;

    margin: auto;
}

.navBarTemplate header img {
    margin-left: 1%;
    width: 200px;
    padding: 15px 0;
}

.navBarTemplate header nav ul {
    display: flex;
}

.navBarTemplate header nav ul li {
    list-style: none;
}

.navBarTemplate header nav ul li a {
    text-decoration: none;
    color: var(--light-green);
    font-size: 1rem;
    padding: 2rem 1rem 1.95rem;
    transition: all 250ms linear 0s;
}

.navBarTemplate header nav ul li a:hover {
    background: rgba(255, 255, 255, 0.15);
}

@media (max-width: 770px) {

    .navBarTemplate nav {
        display: none;
    }

    .navBarTemplate .one,
    .navBarTemplate .two,
    .navBarTemplate .three {
        background-color: var(--light-green);
        height: 5px;
        width: 100%;
        margin: 6px auto;

        transition-duration: 0.3s;
    }

    .navBarTemplate .menu-toggle {
        width: 40px;
        height: 30px;
        margin-right: 10px;
    }

    .navBarTemplate .menu-section.on {
        position: absolute;
        top: 0;
        left: 0;

        width: 100vw;
        height: 100vh;

        background-color: var(--grey);

        z-index: 10;

        display: flex;
        justify-content: center;
        align-items: center;
    }

    .navBarTemplate .menu-section.on nav {
        display: block;
    }

    .navBarTemplate .menu-section.on .menu-toggle {
        position: absolute;
        right: 25px;
        top: 15px;
    }

    .navBarTemplate .menu-section.on .menu-toggle .one {
        transform: rotate(45deg) translate(7px, 7px);
    }

    .navBarTemplate .menu-section.on .menu-toggle .two {
        opacity: 0;
    }

    .navBarTemplate .menu-section.on .menu-toggle .three {
        transform: rotate(-45deg) translate(8px, -9px);
    }

    .navBarTemplate .menu-section.on nav ul {
        text-align: center;
        display: block;
    }

    .navBarTemplate .menu-section.on nav ul a {
        transition-duration: 0.5s;
        font-size: 2rem;
        line-height: 1rem;
        display: block;
    }
}

